<template>
  <div class="upload-modal-overlay" v-if="visible" @click.self="$emit('close')">
    <div class="upload-modal-content">
      <Dashboard
        v-if="uppy"
        :uppy="uppy"
        :props="{
          proudlyDisplayPoweredByUppy: false,
          theme: 'light',
          note: '仅支持 PDF 文件, 最大 1000MB',
          width: '100%',
          height: 450
        }"
      />
    </div>
  </div>
</template>

<script>
import { shallowRef, onUnmounted, watch } from 'vue';
import Uppy from '@uppy/core';
import Tus from '@uppy/tus';
import { Dashboard } from '@uppy/vue';

import '@uppy/core/dist/style.min.css';
import '@uppy/dashboard/dist/style.min.css';

const VUE_APP_API_URL = process.env.VUE_APP_API_URL || 'http://localhost:3000';

export default {
  name: 'UppyUploaderModal',
  components: { Dashboard },
  props: {
    visible: Boolean,
  },
  emits: ['close', 'upload-success'],
  setup(props, { emit }) {
    const uppy = shallowRef(null);

    const setupUppy = () => {
      const uppyInstance = new Uppy({
        debug: true,
        autoProceed: true,
        restrictions: { maxFileSize: 1000 * 1024 * 1024, allowedFileTypes: ['.pdf'] },
      });
      
      uppyInstance.use(Tus, {
        endpoint: `${VUE_APP_API_URL}/api/reports/upload`, 
        retryDelays: [0, 1000, 3000, 5000],
        chunkSize: 5 * 1024 * 1024,
        onBeforeRequest: (req) => {
          const file = uppyInstance.getFile(req.getFileState().id);
          req.setHeader('X-File-Name', file.name);
        },
      });

      uppyInstance.on('complete', (result) => {
        if (result.successful.length > 0) {
          console.log('Uppy: Upload complete!', result.successful);
          emit('upload-success');
          emit('close');
        } else {
          console.error('Uppy: Upload failed', result.failed);
        }
      });
      
      uppy.value = uppyInstance;
    };
    
    const teardownUppy = () => {
      if (uppy.value) {
        uppy.value.close({ reason: 'unmount' });
        uppy.value = null;
      }
    };

    watch(() => props.visible, (isVisible) => {
      if (isVisible) {
        setupUppy();
      } else {
        teardownUppy();
      }
    });

    onUnmounted(() => {
      teardownUppy();
    });

    return { uppy };
  }
};
</script>

<style scoped>
.upload-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1001;
}

.upload-modal-content {
  background: white;
  border-radius: 8px;
  width: 750px;
  max-width: 90%;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  padding: 0;
}
</style>