<template>
  <div class="report-view-wrapper">
    <div class="table-wrapper">
      <ChartSpinner :visible="isLoading" :show-watermark="false" />

      <table class="data-table report-table" v-if="!isLoading && reports.length > 0">
        <thead>
          <tr>
            <th>序号</th>
            <th>PDF名称</th>
            <th>上传时间</th>
            <th>文件大小</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(report, index) in reports" :key="report.name">
            <td class="report-table-cell-center">{{ index + 1 }}</td>
            <td>
              <a :href="getReportDownloadUrl(report.name)" target="_blank" class="report-link">
                {{ report.name }}
              </a>
            </td>
            <td>{{ formatToDateTimeSec(report.uploadTime) }}</td>
            <td>{{ formatFileSize(report.size) }}</td>
            <td class="report-table-cell-center">
              <button @click="$emit('delete-report', report.name)" class="action-btn delete-btn-small">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
      
      <p v-else-if="!isLoading && reports.length === 0" class="no-data">暂无报告文件。</p>
    </div>

    <div class="export-controls">
      <button class="upload-btn" @click="$emit('open-upload-modal')">上传文件</button>
    </div>
  </div>
</template>

<script>
import ChartSpinner from '@/components/common/ChartSpinner.vue'; 

export default {
  name: 'ReportManager',
  components: { ChartSpinner },
  props: {
    reports: {
      type: Array,
      required: true,
    },
    isLoading: {
      type: Boolean,
      required: true,
    }
  },
  emits: ['delete-report', 'open-upload-modal'],
  methods: {
    getReportDownloadUrl(filename) {
      return `/api/reports/download/${encodeURIComponent(filename)}`;
    },

    formatFileSize(bytes) {
      if (bytes == null || isNaN(bytes)) return 'N/A';
      if (bytes === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },

    formatToDateTimeSec(dateString) {
      if (!dateString) return 'N/A';
      const date = new Date(dateString);
      if (isNaN(date.getTime())) return 'Invalid Date';
      const Y = date.getFullYear();
      const M = String(date.getMonth() + 1).padStart(2, '0');
      const D = String(date.getDate()).padStart(2, '0');
      const h = String(date.getHours()).padStart(2, '0');
      const m = String(date.getMinutes()).padStart(2, '0');
      const s = String(date.getSeconds()).padStart(2, '0');
      return `${Y}-${M}-${D} ${h}:${m}:${s}`;
    },
  }
};
</script>

<style scoped>
.table-wrapper {
  margin-bottom: 20px;
  margin-top: 40px;
  overflow-x: auto;
}

.data-table {
  border-collapse: collapse;
  width: 100%;
  font-size: 14px;
  white-space: nowrap;
}

.data-table th,
.data-table td {
  border: 1px solid #ccc;
  padding: 8px 12px;
  text-align: left;
  color: #1e4462;
  vertical-align: middle;
  word-break: break-word;
  white-space: normal;
}

.data-table th {
  background-color: #f4f4f4;
}

.report-table th,
.report-table td {
  padding: 12px 15px;
}

.report-table .report-table-cell-center {
  text-align: center;
}

.report-table th:first-child,
.report-table td:first-child {
  width: 80px;
  text-align: center;
}

.report-table th:last-child,
.report-table td:last-child {
  width: 120px;
  text-align: center;
}

.report-link {
  color: #1e4462;
  text-decoration: none;
  font-weight: bold;
}

.report-link:hover {
  text-decoration: underline;
  color: #1e4462;
}

.no-data {
  color: #1e4462;
  margin-top: 20px;
  text-align: center;
  padding: 40px;
}

/* === 操作和上传按钮 === */
.export-controls {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  gap: 10px;
}

.upload-btn {
  background-color: #1e4462;
  color: white;
  border: 1px solid transparent;
  padding: 8px 16px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.upload-btn:hover {
  background-color: white;
  color: #1e4462;
  border: 1px solid #1e4462;
}

.action-btn {
  font-size: 12px !important;
  padding: 4px 8px !important;
  min-width: auto !important;
  height: auto !important;
  line-height: normal !important;
  border-radius: 3px;
  border: 1px solid transparent;
  cursor: pointer;
  color: white !important;
  transition: opacity 0.2s ease;
}

.action-btn:hover {
  opacity: 0.85;
}

.delete-btn-small {
  background-color: #f56c6c !important; /* Element UI Danger color */
  border-color: #f56c6c !important;
}
</style>